label {
	position: relative;
	right: 0;
	top: 0;
	display: flex;
	align-items: center;
	overflow: hidden;
	cursor: pointer;
	background-color: rgba(19, 19, 19, 0.487);
	border-radius: 30px;
	box-shadow: inset 2px 3px 3px #0003;
}

label i {
	font-size: 1.9rem;
	padding: 15px;
	color: white;
}

input {
	height: 100%;
	width: 0;
	padding: 15px 0;
	padding-right: 0px;
	outline: none;
	font-size: 17px;
	border: 0;
	font-family: inherit;
	background-color: transparent;
	color: white;
	transition: width .2s ease;
}


input:valid,
input:focus {
	width: 280px;
	padding-right: 15px;
}
