.card {
	--x: 0deg;
	--y: 0deg;
	position: relative;
	display: flex;
	flex-direction: column;

	gap: 10px;
	min-width: 300px;
	flex-basis: calc(20% - 30px*0.80);
	perspective: 500;
	padding: 30px 30px 50px 30px;
	text-align: center;
	border-radius: 30px;
	background-color: rgba(19, 19, 19, 0.487);
	transform: rotateY(0deg) rotateX(0deg);
	color: white;
	cursor: pointer;
	transition: transform .2s ease;
}

.card[hide] {
	display: none;
}

.card img {
	width: 100%;
	transform-style: preserve-3d;
	transform: rotateY(0deg) rotateX(0deg);
}

.card:hover img {
	transform: rotateY(var(--x)) rotateX(var(--y)) translateZ(80px) scale(1.25);
}


.card .circle {
	content: '';
	display: block;
	position: absolute;
	top: calc(38% - 95px);
	left: calc(50% - 95px);
	height: 190px;
	width: 190px;
	z-index: -1;
	border-radius: 95px;
	transform: rotateY(0deg) rotateX(0deg);
}

.card:hover .circle {
	transform: rotateY(var(--x)) rotateX(var(--y)) translateZ(30px) scale(1.1);
}

.card div {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: center;
	gap: 10px;
}

.card div.imgContent {
	width: 100%;
	height: 275.8px;
	gap: 0;
}

.card div.imgContent img {
	height: 100%;
}

.card h1 {
	overflow: hidden;
	white-space: nowrap
}

.card div span {
	font-size: 0.95rem;
	padding: 5px 10px;
	border-radius: 7px;
}

.card * {
	transition: transform .2s ease;
	pointer-events: none;
}

.card::after {
	content: 'More details';
	position: absolute;
	height: 0px;
	left: 0;
	right: 0;
	bottom: 0;
	overflow: hidden;
	transition: height .2s ease;
}

.card:hover::after {
	height: 35px;
}