.nextPokemos {
	position: absolute;
	bottom: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 0 auto;
	height: 100px;
	transform: translateY(90px);
	left: calc(50% - 90px);
	transition: transform .3s ease;
	overflow: hidden;
}


.nextPokemos button {
	outline: none;
	padding: 10px 20px;
	font-size: 20px;
	background-color: rgba(38, 38, 38, 0.648);
	color: white;
	border: 0;
	border-radius: 50px;
	cursor: pointer;
	transition: .1s ease;
	transition-property: background-color, transform;
}

.nextPokemos button:hover {
	background-color: rgba(38, 38, 38, 0.902);
}

