.modal {
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	display: grid;
	place-items: center;
	padding: 40px;
	height: 100vh;
	overflow: overlay;
	visibility: hidden;
	backdrop-filter: blur(2px);
	background-color: rgba(19, 19, 19, 0.512);
	opacity: 0;
	animation: showModal .3s ease forwards;
}

@keyframes showModal {

	to{
		opacity: 1;
		visibility: visible;
	}

}

.modal.hide {
	animation: hideModal .3s ease forwards;
}

@keyframes hideModal {

	to{
		opacity: 0;
		visibility: hidden;
	}

}
@media (max-width: 600px) {
	.modal .window {
		width: 100%;
	}
	.modal {
		padding: 40px 5px;
	}
	body > .head {
		flex-direction: column;
		padding-bottom: 90px;
	}
	body .head label {
		position: relative;
	}
}

.modal.open {
	visibility: visible;
	opacity: 1;
}

.modal .window {
	position: relative;
	width: 500px;
	max-width: 90% !important;
	padding: 20px 30px;
	background-color: rgb(38, 38, 38);
	border-radius: 20px;
	color: white;
	transform: translateY(50vh);
	animation: showWindowModal .25s  ease forwards;
}


@keyframes showWindowModal {

	to{
		transform: translateY(0vh);
	}

}

.modal.hide .window {

	animation: hideWindowModal .25s  ease forwards;

}

@keyframes hideWindowModal {

	to{
		transform: translateY(50vh);
	}

}

.modal .window .img img {
	width: 100%;
}

.modal .window h1 {
	color: white;
}

.modal .window i {
	position: absolute;
	top: 10px;
	right: 10px;
	font-size: 2.4rem;
	cursor: pointer;
}

.modal .window .content>div {
	padding: 20px 0;
	display: flex;
	flex-wrap: wrap;
	gap: 5px;
}

.modal .window .content>div h2 {
	margin-bottom: 0px;
	width: 100%;
}

.modal .window .content>div h3 {
	margin-right: 20px;
}

.modal .window .content span {
	padding: 10px 20px;
	border-radius: 7px;
}